@import url(http://fonts.googleapis.com/earlyaccess/nanumgothic.css);
body{margin:0;font-family:'Nanum Gothic';-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:100%;height:100vh;overflow:hidden}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{position:fixed;text-align:center;width:100%;height:100vh;display:flex;flex-direction:column}.App .App-header{width:100%;z-index:500;background-color:#464646;min-height:60px;display:flex;align-items:center;justify-content:flex-start;font-size:calc(10px + 2vmin);box-shadow:0 4px 2px -2px gray;color:white;font-weight:bold}.App .App-header .logo{margin-left:1rem}.App .contents{display:flex;height:100%;position:relative;overflow:hidden;flex:1 1}.App .contents .mode-change-button{width:40px;height:40px;position:absolute;top:40px;left:calc(75% - 40px);z-index:600;background-color:white;border:none;border-radius:0.25rem 0 0 0.25rem;box-shadow:0 0 2px 2px #8080806b;-webkit-clip-path:inset(-2px 0px -2px -2px);clip-path:inset(-2px 0px -2px -2px);color:#8080806b;cursor:pointer}@media (max-width: 767px){.contents{flex-direction:column-reverse}.contents .mode-change-button{display:none}}

.map-container{width:75%;height:100%}.map-container .leaflet-container{width:100%;height:100vh;background-color:black}.map-container .leaflet-container .leaflet-control-container{display:none}.map-container .leaflet-container .leaflet-tooltip-top::before{margin-bottom:-6px !important;border-top-color:#1478ff !important}.map-container .leaflet-container .leaflet-tooltip{background-color:#00000000 !important;border-style:none !important;box-shadow:none !important}.map-container .leaflet-container .leaflet-tooltip>div{background-color:white;border-color:#1478ff;display:flex;align-items:center;border-radius:50px;padding:5px;border-width:2px;border:3px solid #1478ff}.map-container .leaflet-container .leaflet-tooltip>div div span{text-align:left}.map-container .leaflet-container .leaflet-tooltip>div div span:first-child{font-size:0.85rem;font-weight:bold}.map-container .leaflet-container .leaflet-tooltip>div div span:last-child{font-size:0.6rem}.map-container .leaflet-container .leaflet-tooltip>div div:first-child{padding:7px;border-radius:100%;display:flex;background-color:#147aff}.map-container .leaflet-container .leaflet-tooltip>div div:nth-child(2){margin-left:7px;margin-right:7px;display:flex;flex-direction:column;color:black}.map-container .leaflet-container .leaflet-tooltip>div.select{background-color:#1478ff !important;border-style:none !important}.map-container .leaflet-container .leaflet-tooltip>div.select div:first-child{background-color:white}.map-container .leaflet-container .leaflet-tooltip>div.select div:nth-child(2){color:white}.map-container .leaflet-container .leaflet-tooltip .busIcon{width:20px;height:20px;padding:2px}.map-container .alert-box{margin:10px;border-radius:10px;background-color:#6773a988;position:absolute;bottom:0;right:25%;z-index:500;width:250px;height:100px;display:flex;justify-content:center;align-items:center}.map-container .alert-box h5{color:white}.map-container .leaflet-container span{font-weight:bold}@media (max-width: 767px){.map-container{width:100%;height:100%}.map-container .alert-box{width:60%;right:0;top:0}}

.information-container{width:25%;height:100%;background-color:white;z-index:500;box-shadow:-4px 0 2px -2px #8080806b;display:flex;flex-direction:column}.information-container .bus-stop-list{overflow:auto}.information-container .loading{height:100%;display:flex;justify-content:center;align-items:center}.information-container .bus-arraval-list{padding:1rem;overflow-y:auto}.information-container .bus-arraval-list svg{width:20px;height:20px;padding-top:0.2rem}.information-container .bus-arraval-list>div{border-bottom:1px solid #dcdcdc;padding:0.75rem 1rem;display:flex}.information-container .bus-arraval-list>div .info-div{margin-left:1rem;flex:1 1}.information-container .bus-arraval-list>div:first-child{border-top:1px solid #dcdcdc}.information-container .bus-arraval-list div{text-align:left;cursor:pointer}.information-container .bus-arraval-list .route-info{display:flex;align-items:flex-start;justify-content:space-between}.information-container .bus-arraval-list .route-info .route-number{font-size:1.25rem;font-weight:bold;padding-bottom:0.25rem}.information-container .bus-arraval-list .route-info .route-start-end{color:gray;font-size:0.65rem}.information-container .bus-arraval-list .arraval-info{font-size:0.85rem}.information-container .bus-arraval-list .arraval-info span{margin-right:0.5rem;font-weight:bold}.information-container .bus-arraval-list .arraval-info span:first-child{color:#CC1100}.information-container .route-container{display:flex;flex-direction:column;overflow:hidden}.information-container .route-container .route-info-container{margin:0.25rem;font-size:0.75rem}.information-container .route-container .route-info-container .route-info-item{display:flex;padding:0.25rem}.information-container .route-container .route-info-container .title{margin-right:1rem;font-weight:bold}.information-container .route-list{display:flex;flex-direction:column;overflow-y:auto;padding:1rem}.information-container .route-list .route-item{display:flex}.information-container .route-list .route-item .bus-stop-text{flex:1 1;border-bottom:1px solid #dcdcdc;padding:0.5rem 0}.information-container .route-list .route-item .bus-stop-text>div{text-align:left;padding:0.2rem}.information-container .route-list .route-item .bus-stop-text .node-name{font-size:0.85rem;font-weight:bold;display:flex;align-items:center}.information-container .route-list .route-item .bus-stop-text .node-name .start-end-label{border:1px solid #146ACC;color:#146ACC;border-radius:1rem;padding:0.15rem 0.3rem;font-size:0.75rem;margin-left:0.25rem}.information-container .route-list .route-item .bus-stop-text .node-number{font-size:0.7rem}.information-container .route-list .route-item .route-line{width:1.5rem;border-right:0.25rem solid #008039;margin-right:2.25rem;position:relative}.information-container .route-list .route-item .route-line svg{width:1rem;height:1rem}.information-container .route-list .route-item .route-line .bus-logo{width:1.75rem;height:1.75rem;position:absolute;top:calc(50% - 1.25rem);left:calc(50% - 0.25rem);background-color:white;display:flex;justify-content:center;align-items:center;border:#008039 solid 0.2rem;border-radius:2rem}.information-container .route-list .route-item .route-line .bus-number{font-size:0.5rem;width:3rem;top:calc(50% + 1rem);text-align:center;position:absolute;background-color:white;font-weight:bold;border:#008039 solid 0.1rem}.information-container .route-list .route-item:first-child .bus-stop-text{border-top:1px solid #dcdcdc}.information-container .information-table{width:100%}.information-container .information-table div{cursor:pointer}.information-container .information-table .info{display:flex;flex-direction:row;align-items:center;padding:0.25rem 1rem;border-bottom:1px solid black}.information-container .information-table img{width:40px;height:40px}.information-container .information-table h5{font-weight:bold;text-align:start;margin-left:10px;width:80%}.information-container .bus-stop-container{margin:1rem}.information-container .bus-stop-container .bus-stop-name{font-size:1.5rem;font-weight:bold;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;align-items:center}.information-container .bus-stop-container .bus-stop-name>svg{width:1.25rem;height:1.25rem;margin-right:0.5rem}.information-container .bus-stop-container .bus-stop-information .refresh-time{display:flex;justify-content:right;font-size:0.85rem;padding:0.25rem 0}.information-container .bus-stop-container .bus-stop-information .refresh-time button{background-color:transparent;margin-left:0.2rem;border:none;cursor:pointer}@media (max-width: 767px){.information-container{display:none}}

